body {
    font-family: 'Nunito', sans-serif;
}

.home-logo{ 
    position:relative;
    display: block;
    margin:auto;
    width: 100%;
    height: auto;
    margin-top:20px;
}

.home-textbox-1{
    display: inline-block;
    background-color: #F5B295;
    width: 100%;
    height: 100%;
    padding: 25px 25px;
    text-align: center;
    font-size: 22px;
    box-shadow: 20px 20px 5px #b8b2b2;

}
.home-textbox-mission{
    display: inline-block;
    background-color: #F5B295;
    width: 100%;
    padding: 15px 25px;
    text-align: center;
    font-size: 28px;
    box-shadow: 20px 20px 5px #b8b2b2;
    margin-top:20px;

}
.home-textbox-feedback{
    display: inline-block;
    background-color: #F5B295;
    width: 100%;
    padding: 15px 25px;
    text-align: center;
    font-size: 24px;
    box-shadow: 20px 20px 5px #b8b2b2;
}



.box-outline{
    padding-left: 30px;
    padding-right: 30px;
    padding-top:50px;
}
.feedback-outline{
    margin-top: 50px;
    margin-bottom: 50px;
    padding-left: 70px;
    padding-right: 100px;
    
}
.image-outline{
    padding-left: 50px;
    padding-right: 50px;
}
.pic-people{
    display: inline-block;
    background-color: #b8b2b2;
    width: 100%;
    height: auto;
    padding: 15px 25px;
    text-align: center;
    font-size: 20px;
    border: 3px solid rgb(128, 126, 126);
}

@media(max-width: 992px){
    .home-textbox-1{
        margin-top:50px;
        height:auto;
    }
    .home-textbox-mission{
        margin-top:50px;
    }
    .image-outline{
        margin-top:50px;
        padding-left:0px;
        padding-right:0px;
    }
    .feedback-outline{
        padding-left:50px;
        padding-right:50px;
        margin-bottom:100px;
        margin-top:100px;
    }
    .home-logo{
        width: 50%; 
    }

}
.home-textbox-feedback p{
    font-size: 22px;
}
@media(max-width:576px){
    .home-textbox-1{
        font-size: 18px;
    }
    .home-textbox-feedback p{
        font-size: 18px;
    }
    .home-textbox-mission{
        font-size: 22px;
    }
}

.home-padding{
    padding-right: 20px;
}
.space80{
    padding-top: 80px;
}
